.board-content-wrapper {
    background-color: $primary-background-color;
    border-radius: 8px;
    color: $table-text-main-color;
    // margin-inline-end: 10px;
    background-color: #fff;
    border-radius: 8px;

    & form {


        & input {
            width: 100%;
        }

        >* {
            height: 100%;
        }

        & button {
            height: inherit;
            // border-radius: 4px;
            background-color: #1976d2;
            color: #fff;
            padding: 0px 10px;
            position: absolute;
            right: 28px;

            &:hover {
                background-color: #1565c0;
            }

            & .task-arrow-div {
                width: 30px;
                height: 20px;
                flex-basis: 27px;
                color: white;
                text-align: center;
                margin: 5px;
                border-radius: 15%;

                &:hover {
                    color: black;
                    background-color: rgb(177, 173, 173);
                }
            }
        }
    }
}




.group-header-wrapper {
    display: flex;
    flex-direction: column;
    font-weight: 500;
    width: 100%;
    margin-bottom: 50px;
    border: 0;
    vertical-align: baseline;
    justify-content: center;
    // font-size: 1.2rem;
}




.group-header-component {
    // display: flex;
    // justify-content: space-between;
    // padding: 0 11px;
    // min-width: 623px;
    // margin-inline-end: 15px;
    display: grid;
    grid-template-columns: 300px 1fr;
