}

.group-main-container {
    width: 100%;
    overflow-x: auto;
    overflow-y: auto;
    height: 100%;
    padding: 16px 30px 0 18px;
}

.task-row-component {
    background-color: #f5f6f8;
    border-bottom: 1px solid lightgray;
