.status {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 35px;
    color: whitesmoke;
    font-weight: 500;
    position: relative;
    overflow: hidden;
    transition: border-radius 0s;
}

.status:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    border-width: 0px;
    border-style: solid;
    width: 0;
    border-color: #e6e9ef #e6e9ef rgba(255, 255, 255, 0.3) rgba(255, 255, 255, 0.3);
    transition: border-width .2s;
}

.status:hover:before {
    border-width: 5px;
    box-shadow: -5px 5px 10px -6px #000;
}

.column-footer {
    width: 130px;
}






.task-arrow-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 30px;
    position: absolute;
    left: -25px;
}

.task-arrow-div {
    display: none;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    background-color: #dcdfec;
    height: 18px;
    width: 18px;
    font-size: 15px;
}

.user-image-icon-assign {
    border-radius: 50%;
    border: 1px solid #fff;
    height: 30px;
    width: 30px;
    position: absolute;
    transform: translate(-50%);
}

.user-image-wrapper {
    position: relative;
    height: 100%;
    display: flex;
    align-items: center;
}


.column-header {
    position: relative;

    .column-header-bottom {
        justify-content: center;
        width: 100%;

        .drag-handle-col {
            display: none;
        }
    }


    &:hover {
        background-color: #f5f6f8;

        .column-header-bottom {
            justify-content: space-between;
        }

        .drag-handle-col {
            display: block;
        }

        .hide-sort {
            display: block;
        }

        .col-arrow-container {
            display: block;
        }

        & .sort-header-menu {
            position: absolute;
            top: -25%;
            background-color: #ffffff;
            font-size: 1rem;
            padding: 2px;
            border-radius: 50%;
            box-shadow: 0px 0px 5px rgba($color: #000000, $alpha: 0.5);

            &:hover {
                background-color: #1976d2;
                color: #ffffff;
                cursor: pointer;
            }

            &:focus {
                background-color: #1976d2;
            }
        }
    }
}

.hide-sort {
    display: none;
}

.col-arrow-container {
    display: none;
    // float: right;
    // position: absolute;
    // right: 5px;
    border-radius: 4px;
    transition: .1s;
    cursor: pointer;

    &:hover {
        background-color: #e6e9ef;
    }
}

.column-header-title {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;

    &:hover {
        background-color: unset;
        border-inline-end: 4px solid #f5f6f8;

        .drag-handle-group {
            display: block;
        }
    }

    & .sort-header-menu {
        position: absolute;
        right: 50%;
    }
}

.drag-handle-group {
    display: none;
    margin-inline-end: 5px;
}

.editable-column-header {
    padding: 3px;

    &:hover {
        border: 1px dashed rgb(168, 168, 168);
    }
}

.flex-row-items {
    & input {
        position: absolute;
        width: 130px;
        height: 100%;
        opacity: 0;
    }

    & input[type="date"]::-webkit-inner-spin-button,
    input[type="date"]::-webkit-calendar-picker-indicator {
        cursor: pointer;
        width: 100%;
        opacity: 0;
    }

}

